const { ccclass, property } = cc._decorator;

@ccclass
export class UIController extends cc.Component {

    Grid: cc.Node = null;


    protected start(): void {
        this.Grid = this.node.getChildByName("Grid");
        // console.log(this.resultNode);

        this.handleYourOrientation();

        cc.view.setResizeCallback(() => {
            this.handleYourOrientation();
        });
    }


    private handleYourOrientation() {
        let device = cc.view.getFrameSize();
        if (device.width > device.height) {
            this.node.scale = 1;
        } else {
            this.node.scale = 2.3;
        }
    }

}
